<script>
	export default {
		onLaunch: function() {
			// 检测是否更新
			const updateManager = uni.getUpdateManager();
			// 更新提示
			updateManager.onUpdateReady(function(res) {
				uni.hideLoading();
				uni.showModal({
					title: '更新提示',
					content: '新版本已经准备好，是否重启应用？',
					success(res) {
						if (res.confirm) {
							// 新的版本已经下载好，调用 applyUpdate 应用新版本并重启
							updateManager.applyUpdate();
						}
					}
				});
			});
		},
		onShow: function() {
			
		},
		onHide: function() {
			console.log('App Hide')
		}
	}
</script>

